import React, { FC, useState, useEffect, useRef, useCallback } from 'react';
import clsx from 'clsx';
import { useGeolocation } from 'react-use';
import * as searchActions from '../../store/Search/actions';
import { ReduxStore, ShopState, AppLatLng } from '../../store/types';
import { useSelector, useDispatch, shallowEqual } from 'react-redux';
import 'leaflet/dist/leaflet.css';
import { Box, Button, makeStyles, Fab, Snackbar } from '@material-ui/core';

// Leaflet map
import { Map } from 'react-leaflet';
import 'leaflet/dist/leaflet.css';

// Map
import MapComponent from './MapComponent';

// Search bar
import SearchBar from './SearchBar';

import Swiper, { SwiperInstance } from 'react-id-swiper';
import 'react-id-swiper/lib/styles/scss/swiper.scss';
import ShopInfoCard from 'components/ShopInfoCard';
// import { closeNavMenu } from 'store/NavMenu/actions';
import { distance } from 'helpers';

import { useSnackbar } from 'notistack';

const useStyles = makeStyles(theme => ({
  overlayElement: {
    zIndex: 500,
  },
  searchThisAreaButton: {
    marginTop: theme.spacing(1),
    color: '#fff',
  },
  fab: {
    marginLeft: 'auto',
    marginTop: theme.spacing(2),
    backgroundColor: '#fff',
  },
  fabDisabled: {
    backgroundColor: '#ddd',
  },
  fabIcon: {
    fontSize: '24px',
    color: theme.palette.primary.main,
  },
  fabIconDisabled: {
    color: '#aaa',
  },
}));

// retrieve default zoom level from env
const defaultZoom = parseInt(process.env.REACT_APP_MAP_ZOOM || '15');
//retrieve default lat lng from env
const latlngStr = process.env.REACT_APP_LATLNG || '22.28552,114.15769';
const defaultCenter: AppLatLng = {
  lat: parseFloat(latlngStr.split(',')[0]),
  lng: parseFloat(latlngStr.split(',')[1]),
};

const MapView: FC = props => {
  // Snackbar hook
  const { enqueueSnackbar } = useSnackbar();

  // Styles
  const classes = useStyles();

  // Dispatch
  const dispatch = useDispatch();
  // Redux states
  const searchState = useSelector((state: ReduxStore) => state.searchState);
  const bookmarkShopIds = useSelector(
    (state: ReduxStore) => state.bookmark.shopIds
  );

  // Map ref object
  let mapRef = useRef<Map>(null);

  // Swiper ref object to scroll programmatically
  const swiperRef = useRef<SwiperInstance>(null);

  // use user geolocation. will ask for permission here
  const {
    latitude: userLat,
    longitude: userLng,
    error: userLatLngError,
  } = useGeolocation({
    maximumAge: 1000,
  });
  const userLatLng = userLat && userLng ? { lat: userLat, lng: userLng } : null;

  // State to store position info, default to default center
  const position = { center: searchState.center, zoom: searchState.zoom };

  // If user have moved the map, "search this area will appear"
  const [moved, setMoved,] = useState(false);

  // Local search term. Only dispatch to redux store when perform search
  const [searchTerm, setSearchTerm,] = useState(searchState.term);

  // Handle Input change: change local state first
  const handleSearchTermChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    setSearchTerm(e.target.value);
  };

  // When user press "GPS" button
  const handleGPSPress = useCallback(() => {
    if (userLatLng) dispatch(searchActions.setPosition({ center: userLatLng }));
  }, [dispatch, userLatLng,]);

  // Dispatch searching with searchTerm and current map center
  const handleQuery = useCallback(
    (initialSearch: boolean) => {
      // dispatch(closeNavMenu());
      if (!initialSearch && searchTerm.trim() === '') {
        return;
      }
      if (mapRef.current) {
        const bounds = mapRef.current.leafletElement.getBounds();
        dispatch(searchActions.handleSearch(searchTerm,
          bounds.getNorthEast(), bounds.getSouthWest()));
        setMoved(false);
      }
    },
    [dispatch, searchTerm,]
  );

  const loadStatus = useRef({ firstLoaded: false, firstFetched: false });
  useEffect(() => {
    if (!loadStatus.current.firstLoaded) {
      // Before Location is loaded
      if (userLatLng) {
        // If sucessfully get location
        // Move to user location
        if (searchState.term === '') {
          dispatch(searchActions.setPosition({ center: userLatLng }));
        }
        // mark as loaded

        // `setPosition` will trigger a re-render,
        // and this effect will be run again,
        // proceed to second half
        loadStatus.current.firstLoaded = true;
      } else if (userLatLngError) {
        // If fail to get user location,
        // `setPosition` will not be called and therefore
        // no re-render

        // Directly handleQuery to search around default center
        loadStatus.current.firstLoaded = true;
        handleQuery(true);
        loadStatus.current.firstFetched = true;
      }
    } else if (!loadStatus.current.firstFetched) {
      // After first `setPosition` and search around user location
      if (
        searchTerm === '' &&
        userLatLng &&
        distance(position.center, userLatLng) < 0.1
      ) {
        handleQuery(true);
        loadStatus.current.firstFetched = true;
      }
    }
  }, [
    dispatch,
    handleQuery,
    position.center,
    searchState.term,
    searchTerm,
    userLatLng,
    userLatLngError,
  ]);

  // Select shop when pin press
  const handlePinPress = (index: number) => {
    if (swiperRef.current) {
      swiperRef.current.slideTo(index);
    }
  };

  // Scroll to shop when shop selected
  // useEffect(() => {
  //   if (swiperRef.current && searchState.selectedShopIndex >= 0) {
  //     swiperRef.current.slideTo(searchState.selectedShopIndex);
  //   }
  // }, [searchState,]);

  // Show snackbar when noresult
  useEffect(() => {
    if (searchState.result.length === 0 && searchState.term !== '') {
      enqueueSnackbar('找不到任何商戶', {
        autoHideDuration: 1000,
        variant: 'error',
      });
    }
  }, [enqueueSnackbar, searchState.result, searchState.term,]);

  // Store current slide index
  const handleSwiperSlide = () => {
    if (swiperRef.current) {
      dispatch(
        searchActions.setSelectedShopIndex(swiperRef.current.activeIndex)
      );
    }
  };

  const handlePositionChange = ({
    center,
    zoom,
  }: {
    center: AppLatLng;
    zoom: number;
  }) => {
    if (center && zoom) {
      dispatch(searchActions.setPosition({ center, zoom }));
    }
  };

  return (
    <>
      <Box
        position='absolute'
        top={0}
        left={0}
        px={2}
        py={3}
        width='100vw'
        height='100%'
        display='flex'
        flexDirection='column'
        alignContent='stretch'
      >
        <Box>
          <SearchBar
            searchTerm={searchTerm}
            handleSearchTermChange={handleSearchTermChange}
            handleQuery={() => handleQuery(false)}
            className={classes.overlayElement}
          />
        </Box>
        {moved && (
          <Box>
            <Button
              variant='contained'
              color='primary'
              className={clsx(
                classes.overlayElement,
                classes.searchThisAreaButton
              )}
              onClick={() => handleQuery(false)}
            >
              此範圍內搜尋
            </Button>
          </Box>
        )}
        <Box flex={1} />
        <Box ml='auto'>
          <Fab
            disabled={!userLatLng}
            color='inherit'
            className={clsx(classes.overlayElement, classes.fab, {
              [classes.fabDisabled]: !userLatLng,
            })}
            onClick={handleGPSPress}
          >
            <i
              className={clsx(classes.fabIcon, 'icon-gps', {
                [classes.fabIconDisabled]: !userLatLng,
              })}
            />
          </Fab>
        </Box>
        {/* TODO: Hide slider when no focus */}
        <Box mx={-2} py={1} className={classes.overlayElement}>
          {searchState.result.length > 0 && (
            <Swiper
              on={{
                slideChange: handleSwiperSlide,
              }}
              slidesPerView={1.2}
              centeredSlides
              spaceBetween={10}
              getSwiper={swiper => {
                swiperRef.current = swiper;
              }}
              shouldSwiperUpdate
            >
              {searchState.result.map(shop => (
                <ShopInfoCard
                  className={classes.overlayElement}
                  key={shop.id}
                  shop={shop}
                  refLatLng={userLatLng || defaultCenter}
                  bookmarked={bookmarkShopIds.includes(shop.id)}
                />
              ))}
            </Swiper>
          )}
        </Box>
      </Box>
      <MapComponent
        shops={searchState.result}
        selectedShopIndex={searchState.selectedShopIndex}
        onPinPress={handlePinPress}
        userLatLng={userLatLng}
        center={searchState.center}
        zoom={searchState.zoom}
        handlePositionChange={handlePositionChange}
        mapRef={mapRef}
        onMoved={() => setMoved(true)}
      />
    </>
  );
};

export default MapView;
