import { takeLatest, select, put, call } from 'redux-saga/effects';
import { SagaIterator } from 'redux-saga';
import * as actions from './actions';
import * as types from './actionTypes';
import { search } from './search';
import { ReduxStore } from '../types';
import { ShopStore } from '../Shop/reducers';
import { SearchState } from './reducers';
function* searchDb(action: ReturnType<typeof actions.handleSearch>) {
  const {
    payload: { northEast, southWest },
  } = action;

  const { term, center }: SearchState = yield select(
    (state: ReduxStore) => state.searchState
  );
  const shopsStore: ShopStore = yield select(
    (state: ReduxStore) => state.shops
  );
  const searchResult = search({
    term,
    latlng: center,
    northEast,
    southWest,
    maxResults: 50,
    shops: shopsStore,
  });
  yield put(actions.setSearchResult(searchResult));
}

function* watchSearchDb(): SagaIterator {
  yield takeLatest(types.HANDLE_SEARCH, searchDb);
}

export default [watchSearchDb,];
