import { Reducer } from 'redux';
import * as types from './actionTypes';
import { AppAction, ShopState, AppLatLng } from '../types';
// retrieve default zoom level from env
const defaultZoom = parseInt(process.env.REACT_APP_MAP_ZOOM || '15');
//retrieve default lat lng from env
const latlngStr = process.env.REACT_APP_LATLNG || '22.28552,114.15769';
const defaultCenter: AppLatLng = {
  lat: parseFloat(latlngStr.split(',')[0]),
  lng: parseFloat(latlngStr.split(',')[1]),
};

export interface SearchState {
  term: string;
  center: AppLatLng;
  zoom: number;
  result: ShopState[];
  selectedShopIndex: number;
}

const searchState: Reducer<SearchState, AppAction> = (
  state = {
    term: '',
    center: defaultCenter,
    zoom: defaultZoom,
    result: [],
    selectedShopIndex: -1,
  },
  action
) => {
  switch (action.type) {
  case types.HANDLE_SEARCH:
    return {
      ...state,
      term: action.payload.term,
    };
  case types.SET_SEARCH_RESULT:
    return {
      ...state,
      result: action.payload.searchResult,
      selectedShopIndex: 0,
    };
  case types.SET_SELECTED_SHOP_INDEX:
      return {
        ...state,
        center: state.result[action.payload.index].latlng,
      selectedShopIndex: action.payload.index,
    };
  case types.SET_POSITION:
    return {
      ...state,
      ...action.payload
    };
  
  default:
    return state;
  }
};

export default searchState;
