import React, { FC } from 'react';
import { RouteComponentProps, Link } from 'react-router-dom';
import { toggleNavMenu, closeNavMenu } from 'store/NavMenu/actions';
import { useSelector, useDispatch } from 'react-redux';
import cls from 'classnames';
import './style.scss';
import { ReduxStore } from 'store/types';
import { MenuList, MenuItem, ClickAwayListener } from '@material-ui/core';
import { makeStyles } from '@material-ui/styles';
import clsx from 'clsx';

const useStyles = makeStyles(theme => ({
  // TODO: put all color constants in a common file?
  title: {
    paddingLeft: '20px',
    paddingTop: '20px',
    paddingBottom: '10px',
    color: '#FFB331',
    borderBottom: '1px solid #D8D8D8',
    marginBottom: '0',
  },
  menuList: {
    color: '#8E8E93',
    padding: '0',
  },
  menuItem: {
    paddingTop: '15px',
    paddingBottom: '15px',
    borderBottom: '1px solid #D8D8D8',
    fontSize: '18px',
    '&:hover': {
      backgroundColor: '#FFB331',
      color: '#FFFFFF',
    },
  },
  icon: {
    fontSize: '24px',
    paddingRight: '5px',
  },
}));

const Navigation: FC<RouteComponentProps> = props => {
  const dispatch = useDispatch();
  const store = useSelector((state: ReduxStore) => state.navMenu);
  const classes = useStyles();
  const itemClick = (route: string) => () => {
    dispatch(closeNavMenu());
    props.history.push(route);
  };
  const handleClickAway = () => {
    dispatch(closeNavMenu());
  };
  return (
    <ClickAwayListener onClickAway={handleClickAway}>
      <div
        className={cls('Navigation', {
          'Navigation--hidden': !store.isOpen,
        })}
      >
        <h1 className={classes.title}>自由篩</h1>
        <MenuList className={classes.menuList}>
          <MenuItem
            className={classes.menuItem}
            onClick={itemClick('/bookmarks')}
          >
            <i className={clsx(classes.icon, 'icon-bookmark')} />
            收藏
          </MenuItem>
        </MenuList>
        <div>version: {process.env.REACT_APP_VERSION}</div>
      </div>
    </ClickAwayListener>
  );
};

export default Navigation;
